<template>
	<view class="page-container">
		<view class="info-item">
			<view class="info-name">
				{{user.nickName}}
			</view>
			<view class="info-phone">
				电话：
				<view style="float: right;width: 85%;" @click="callPhone(phone)">
					<text>{{user.phonenumber}}</text>
					<image src="/static/image/phoneImage.png" mode=""></image>
				</view>
			</view>
			<!-- <view class="info-address">
				地址：<text>内蒙古呼和浩特市赛罕区某街道809</text>
			</view> -->
		</view>

		<view class="">
			<!-- 牧草列表 -->
			<view class="grass-list">
				<view class="grass-item" v-for="(item, index) in grassList" :key="index"
					@click="viewGrassDetail(item)">
					<image :src="httpUrl + item.picUrlList.split(',')[0]" mode="aspectFill" class="grass-image"></image>
					<view class="grass-info">
						<view class="grass-title">{{item.type}}</view>
						<!-- <view class="grass-tag"
							:class="{'tag-premium': item.level === '优级', 'tag-first': item.level === '一级', 'tag-second': item.level === '二级'}">
							{{item.level}}
						</view> -->
						<view style="margin-top: 10rpx;margin-bottom: 10rpx;"> 
							{{item.addressNames}}
						</view>
						<view class="grass-detail">
							<text class="grass-weight">重量 <text style="color: #333;padding-left: 20rpx;">
									{{item.number}}吨</text></text>
							<text style="padding-right: 20rpx;">|</text>
							<text class="grass-type">品类 <text style="color: #333;padding-left: 20rpx;">
									{{item.type}}</text></text>
						</view>
					</view>
				</view>
			</view>
		</view>
		<view style="height: 50rpx;"></view>
	</view>
</template>

<script>
	import {
		userApi
	} from '../../../api/user.js';
	import settings from '../../../common/settings.js';
	export default {
		data() {
			return {
				user:{},
				phone: '18410078688',
				// 牧草数据列表
				grassList: [{
						id: 1,
						image: '/static/imgCs/swiper-image.png',
						level: '优级',
						weight: '6',
						type: '燕麦干草'
					},
					{
						id: 2,
						image: '/static/imgCs/swiper-image.png',
						level: '一级',
						weight: '6',
						type: '牧草'
					},
					{
						id: 3,
						image: '/static/imgCs/swiper-image.png',
						level: '二级',
						weight: '6',
						type: '秸秆'
					},
					{
						id: 4,
						image: '/static/imgCs/swiper-image.png',
						level: '优级',
						weight: '6',
						type: '燕麦干草'
					},
					{
						id: 5,
						image: '/static/imgCs/swiper-image.png',
						level: '优级',
						weight: '6',
						type: '燕麦干草'
					},
					{
						id: 6,
						image: '/static/imgCs/swiper-image.png',
						level: '一级',
						weight: '6',
						type: '燕麦干草'
					}
				],
				uId:'',
				httpUrl:'',
			};
		},
		onLoad(options) {
			this.httpUrl = settings.devUrl
			console.log(this.httpUrl)
			this.uId = options.id
			this.gitUserList();	
		},
		methods: {
			gitUserList(){
				userApi.getUserInfo(this.uId).then(res => {
					console.log(res.data)
					this.user = res.data
				})
				userApi.userList(this.uId).then(res => {
					console.log(res)
					this.grassList = res.data
					console.log(this.grassList)
				})
			},
			
			// 拨打手机号
			callPhone(phoneNumber) {
				uni.makePhoneCall({
					phoneNumber: phoneNumber,
					success: () => {
						console.log('拨号成功');
					},
					fail: (err) => {
						console.error('拨号失败', err);
					}
				});
			},
			// 查看牧草详情
			viewGrassDetail(item) {
				uni.showToast({
					title: '查看详情：' + item.level + '牧草',
					icon: 'none'
				})
				uni.navigateTo({
					url: "/subpkg_index/pages/grassDetails/grassDetails?id=" + item.id
				})

			}
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #f5f6f9;
	}

	.page-container {
		padding: 30rpx;

		.info-name {
			font-size: 36rpx;
			font-weight: 500;
			letter-spacing: 0rpx;
			line-height: 46rpx;
			color: rgba(51, 51, 51, 1);
			text-align: left;
			vertical-align: top;
		}

		.info-phone {
			width: 642rpx;
			height: 60rpx;
			opacity: 1;
			background: #EDF5EE;
			border-radius: 14rpx;
			color: #999999;
			padding: 10rpx 16rpx;
			margin: 20rpx 0;

			text {
				color: #3B8043;
			}

			image {
				float: right;
				width: 44rpx;
				height: 44rpx;
			}
		}

		.info-address {
			font-size: 28rpx;
			font-weight: 400;
			letter-spacing: 0rpx;
			line-height: 46rpx;
			text-align: left;
			vertical-align: top;
			color: #999999;

			text {
				color: #333333;
			}
		}

		.info-item {
			background-color: #fff;
			border-radius: 24rpx;
			padding: 30rpx;
			margin-bottom: 20rpx;
		}

		.grass-list {
			.grass-item {
				background-color: #ffffff;
				border-radius: 20rpx;
				padding: 20rpx;
				margin-bottom: 20rpx;
				display: flex;

				.grass-image {
					width: 200rpx;
					height: 160rpx;
					border-radius: 10rpx;
					margin-right: 20rpx;
					object-fit: cover;
					/* 保持比例，填满容器，超出部分裁剪 */
					object-position: center;
					/* 控制裁剪的焦点位置，默认居中 */
				}

				.grass-info {
					flex: 1;
					position: relative;

					.grass-title {
						font-size: 32rpx;
						margin-bottom: 34rpx;
					}

					.grass-tag {
						display: inline-block;
						padding: 4rpx 20rpx;
						border-radius: 8rpx 8rpx 0 0;
						font-size: 24rpx;
						color: rgba(245, 203, 114, 1);
						// margin-bottom: 30rpx;

						&.tag-premium {
							background-color: #5eb375;
						}

						&.tag-first {
							background-color: #5eb375;
						}

						&.tag-second {
							background-color: #5eb375;
						}
					}

					.grass-detail {
						display: flex;
						background-color: #edf5ee;
						padding: 0 10rpx;
						border-radius: 12rpx;
						line-height: 60rpx;
						color: #3B8043;
						font-size: 30rpx;
						font-weight: 500;

						image {
							width: 40rpx;
							height: 40rpx;
							float: left;
							padding-top: 10rpx;
						}

						.grass-weight {
							color: #999;
							font-size: 24rpx;
							margin-right: 20rpx;
						}

						.grass-type {
							color: #999;
							font-size: 24rpx;
						}
					}
				}
			}
		}
	}
</style>